<template>
    <div>
        <Alert type="success">  <code>.env</code>  <code>INDEX_URL</code></Alert>
        <Button type="primary" @click="$router.push('/example')"></Button>
    </div>
</template>

<script>
    import { config } from '../helper'

    export default {
        created() {
            if (config('INDEX_URL') !== "/") {
                this.$router.replace(config('INDEX_URL'))
            }
        },
    }
</script>
